.login{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%), url("http://placekitten.com/900/900") no-repeat;
    background-size: cover;
    position: relative;
    
    .top{
        .wrapper{
            padding: 20px 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .logo{
                height: 40px;
            }
        }
    }
    .container{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: #fff;

        form{
            width: 300px;
            height: 300px;
            padding: 30px;
            border-radius: 5px;
            background-color: var(--main-color);
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            input{
                height: 40px;
                border-radius: 5px;
                background-color: gray;
                color: #fff;
                padding-left: 10px;

                &::placeholder{
                    color: lightgray;
                }
            }
            button{
                height: 40px;
                border-radius: 5px;
                background: red;
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                cursor: pointer;
            }

            span{
                color: lightgray;
                b{
                    color: #fff;
                }
            }
        }
    }
}