.navbar{
    width: 100%;
    color: white;
    font-size: 16px;
    position: fixed;
    top: 0;
    z-index: 9999;
    background: linear-gradient(to top,transparent 0%,rgba(0,0,0,.3), 50%);


    &.scrolled{
        background-color: var(--main-color);
    }
    .container{
        padding: 0px 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 70px;
        .left{
            img{
                height: 25px;
                margin-right: 40px;
            }
            display: flex;
            align-items: center;
            span{
                margin-right: 20px;
                cursor: pointer;
            }
        }
        .right{
            display: flex;
            align-items: center;
            img{
                width: 30px;
                height: 30px;
                border-radius: 5px;
                object-fit: cover;
                cursor: pointer;
            }
            .icon{
                margin:0 15px;
                cursor: pointer;
            }
            .profile{
                .options{
                    display: none;
                    background-color: var(--main-color);
                    border-radius: 5px;
                }

                span{
                    padding: 10px;
                    cursor: pointer;
                }

                &:hover{
                    .options{
                        display: flex;
                        flex-direction: column;
                        position: absolute;
                    }
                }
            }
        }
    }
}