.listitem{
    width: 225px;
    height: 120px;
    background-color: var(--main-color);
    margin-right: 5px;
    cursor: pointer;
    color: #fff;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    video{
        width: 100%;
        height: 140px;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
    }
    &:hover{
        width: 325px;
        height: 300px;
        position: absolute;
        top: -150px;
        box-shadow: 0px 2px 15px 0px #fff;
        -webkit-box-shadow: 0px 2px 15px 0px #fff;
        -moz-box-shadow: 0px 2px 15px 0px #fff;
        border-radius: 5px;

        img{
            height: 140px;
        }
        .itemInfo{
            display: flex;
            flex-direction: column;
            padding: 5px;

            .icons{
                display: flex;
                margin-bottom: 10px;

                .icon{
                    border: 2px solid #fff;
                    padding: 5px;
                    border-radius: 50%;
                    margin-right: 10px;
                    font-size: 16px;
                }
            }
            .itemInfoTop{
                display: flex;
                align-items: center;
                margin-bottom: 10px;
                font-size: 14px;
                font-weight: 600;
                color: gray;

                .limit{
                    border: 1px solid gray;
                    padding: 1px 3px;
                    margin: 0 10px;
                }
            }
            .desc{
                margin-bottom: 10px;
                font-size: 13px;
            }
            .genre{
                font-size: 14px;
                color: lightgray;

                
            }
        }
    }
}